import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';

const ImageSlider = ({ images , interval = 2000}) => {
  const [currentIndex, setCurrentIndex] = useState(0);

  useEffect(() => {
    const sliderInterval = setInterval(() => {
      setCurrentIndex((prevIndex) => (prevIndex === images.length - 1 ? 0 : prevIndex + 1));
    }, interval);
    return () => clearInterval(sliderInterval);
  }, [images.length, interval]);

  const prevSlide = () => {
    setCurrentIndex((prevIndex) => (prevIndex === 0 ? images.length - 1 : prevIndex - 1));
  };

  const nextSlide = () => {
    setCurrentIndex((prevIndex) => (prevIndex === images.length - 1 ? 0 : prevIndex + 1));
  };

  return (
    <div className="relative w-full h-full overflow-hidden images-slider">
        <div className="slider-overlay"></div>
      <motion.div 
        className="flex w-full h-full slider"
        initial={{ x: 0 }}
        animate={{ x: `-${currentIndex * 100}%` }}
        transition={{ duration: 0.5, ease: 'easeInOut' }}
      >
        {images.map((image, index) => (
          <motion.img key={index} src={image} alt={`Slide ${index + 1}`} style={{ minWidth: '100%', minHeight: '100%' }} className="w-full h-full object-cover slider-image" />
        ))}
      </motion.div>
      <button onClick={prevSlide} className="absolute left-2 top-1/2 transform -translate-y-1/2 bg-gray-800 bg-opacity-50 text-white p-2 rounded-full">
        
      </button>
      <button onClick={nextSlide} className="absolute right-2 top-1/2 transform -translate-y-1/2 bg-gray-800 bg-opacity-50 text-white p-2 rounded-full">
        
      </button>
    </div>
  );
};

export default ImageSlider;
